.wp-block-growtype-gallery a.wp-block-image-wrapper {
  opacity: 0.8;
  transition: all 0.5s;
}
.wp-block-growtype-gallery a.wp-block-image-wrapper:hover {
  opacity: 1;
  transition: all 0.5s;
}

.growtype-gallery-wrapper {
  display: inline-block !important;
  width: 100%;
}
.growtype-gallery-wrapper[data-loader-type=story] .loader {
  position: absolute;
  height: 8px;
  width: calc(100% - 60px);
  top: 25px;
  left: 30px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.3);
  display: none;
}
.growtype-gallery-wrapper[data-loader-type=story] .loader .loader-inner {
  width: 0%;
  position: absolute;
  background: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 10px;
}
.growtype-gallery-wrapper[data-slider-overflow=initial] .slick-list {
  overflow: initial;
}

.wp-block-growtype-gallery {
  gap: 0 !important;
}
.wp-block-growtype-gallery .wp-block-image {
  padding: 5px;
  float: left;
  margin: 0;
}
.wp-block-growtype-gallery .wp-block-image.is-style-rounded .wp-block-image-inner {
  border-radius: 500px;
}
.wp-block-growtype-gallery .wp-block-image .watermark {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  color: white;
  font-size: 14px;
  text-align: left;
  padding: 10px 20px;
}
