.growtype-gallery-wrapper {
  &[data-preview-grid-style="1"] {
    figure {
      &:nth-child(4) {
        width: 100%;

        .wp-block-image-inner {
          padding-top: calc(100% / 3 - 10px) !important;
        }
      }

      &:nth-child(5) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }

      &:nth-child(7) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }

      &:nth-child(8) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }

      &:nth-child(10) {
        .wp-block-image-inner {
          padding-top: calc((100% + 5px) * 2) !important;
        }
      }

      &:nth-child(11) {
        width: calc(100% / 3 * 2) !important;

        .wp-block-image-inner {
          padding-top: calc(100% / 2 - 5px) !important;
        }
      }
    }
  }

  &[data-preview-grid-style="2"] {
    figure {
      &:nth-child(3) {
        .wp-block-image-inner {
          padding-top: calc(100% * 2 + 10px) !important;
        }
      }

      &:nth-child(4) {
        width: calc(100% / 3 * 2) !important;

        .wp-block-image-inner {
          padding-top: calc(100% / 2 - 5px) !important;
        }
      }

      &:nth-child(5) {
        width: 100%;

        .wp-block-image-inner {
          padding-top: calc(100% / 3 - 10px) !important;
        }
      }

      &:nth-child(7) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }

      &:nth-child(9) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }

      &:nth-child(10) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5) !important;
        }
      }
    }
  }

  &[data-preview-grid-style="3"] {
    figure {
      &:nth-child(1) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(3) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(5) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(6) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(8) {
        width: 100%;

        .wp-block-image-inner {
          padding-top: calc(100% / 3 - 10px) !important;
        }
      }
    }
  }

  &[data-preview-grid-style="4"] {
    figure {
      &:nth-child(4) {
        width: calc(100% / 3 * 2) !important;

        .wp-block-image-inner {
          padding-top: calc(100% / 2 - 5px) !important;
        }
      }

      &:nth-child(6) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(7) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(8) {
        .wp-block-image-inner {
          padding-top: calc(100% * 1.5 + 5px) !important;
        }
      }

      &:nth-child(9) {
        width: 100%;

        .wp-block-image-inner {
          padding-top: calc(100% / 3 - 10px) !important;
        }
      }

      &:nth-child(11) {
        width: calc(100% / 3 * 2) !important;

        .wp-block-image-inner {
          padding-top: calc(100% / 2 - 5px) !important;
        }
      }
    }
  }
}
